import classNames from 'classnames/bind';

import styles from './index.module.css'

import logo from '../../assets/logo/logo.png'

const cx = classNames.bind(styles);

const Login = () => {
    return (
        <div className={`${cx('wrapper')} d-flex justify-content-center align-items-center`}>
           <div className={`${cx('section-login')}`} >
                <div className={`row h-100`}>
                    <div className={`${cx('section-login-logo')} col`}>
                        <img src={logo}/>
                        <div className='mt-4'>
                            <h1>ng nhp vo ti khon ca bn</h1>
                        </div>
                    </div>
                    <div className={`${cx('section-login-logo')} col d-flex align-items-center`}>
                        <div className='col'>
                            <div className={`row`}>
                                <label className={cx('input-title')}>email/st</label>
                                <input className={cx('login-email')} type='email' name='email' placeholder='Nhp email ca bn'/>
                            </div>
                            <div className={`row mt-4`}>
                                <label className={cx('input-title')}>mt khu</label>
                                <input className={cx('login-password')} type='password' name='password' placeholder='Nhp mt khu ca bn'/>
                            </div>
                            <a href='/home'>
                                <button className={`${cx('btn-login')} float-end`}>
                                    ng nhp
                                </button>
                            </a>
                        </div>
                    </div>
                    <a href='/signup' className={`${cx('signup-option')}`}>
                        Cha c ti khon? NG K NGAY
                    </a>
                </div>
           </div>
        </div>
    )
}

export default Login;